@import './_reset.scss';
$mainColor : #000000;

.red {
    color: $mainColor;
}

div.container {
    h4 {
        color: black;
    }
    
    p {
        color: black;
    }
}


.my-alert{
    background: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

.my-alert2{
    @extend .my-alert;
    background: chartreuse;
}

.my-alert p {
    margin-bottom: 0px;
}

//함수 만드는 문법
@mixin 함수() {
    background: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto; 
}

.my-alert3 {
    @include 함수();
        
    p {
      margin-bottom: 0px;  
    }
}

.loading-alert{
    @extend .my-alert;
}
